import React, { useState, useEffect } from 'react';
import { Save, Trash2, Plus, MapPin, Users, Gift, LogOut, Camera } from 'react-feather';
import './Admin.css';

const Admin = () => {
  const [currentSection, setCurrentSection] = useState('discounts');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loginData, setLoginData] = useState({
    username: '',
    password: ''
  });
  const [discounts, setDiscounts] = useState([]);
  const [profiles, setProfiles] = useState([]);
  const [currentDiscount, setCurrentDiscount] = useState({
    id: null,
    title: '',
    description: '',
    coordinates: '',
    category: 'food',
    discountValue: '',
    isActive: true
  });
  const [currentProfile, setCurrentProfile] = useState({
    id: null,
    name: '',
    telegram: '',
    photo: null,
    photoPreview: null,
    isActive: true
  });
  const [isEditing, setIsEditing] = useState(false);
  const [isEditingProfile, setIsEditingProfile] = useState(false);

  //    
  useEffect(() => {
    const auth = localStorage.getItem('adminAuth');
    if (auth === 'true') {
      setIsAuthenticated(true);
    }
  }, []);

  //  
  useEffect(() => {
    if (!isAuthenticated) return;

    const savedDiscounts = localStorage.getItem('youthCouncilDiscounts');
    if (savedDiscounts) {
      setDiscounts(JSON.parse(savedDiscounts));
    }

    const savedProfiles = localStorage.getItem('youthCouncilProfiles');
    if (savedProfiles) {
      setProfiles(JSON.parse(savedProfiles));
    }
  }, [isAuthenticated]);

  //  
  useEffect(() => {
    if (!isAuthenticated) return;
    localStorage.setItem('youthCouncilDiscounts', JSON.stringify(discounts));
  }, [discounts, isAuthenticated]);

  useEffect(() => {
    if (!isAuthenticated) return;
    localStorage.setItem('youthCouncilProfiles', JSON.stringify(profiles));
  }, [profiles, isAuthenticated]);

  const handleLogin = (e) => {
    e.preventDefault();
    
    if (loginData.username === 'admin' && loginData.password === 'admin123') {
      setIsAuthenticated(true);
      localStorage.setItem('adminAuth', 'true');
    } else {
      alert('   ');
    }
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
    localStorage.removeItem('adminAuth');
  };

  //    
  const handleDiscountSubmit = (e) => {
    e.preventDefault();
    
    if (isEditing) {
      setDiscounts(discounts.map(d => 
        d.id === currentDiscount.id ? currentDiscount : d
      ));
    } else {
      const newDiscount = {
        ...currentDiscount,
        id: Date.now().toString(),
        createdAt: new Date().toISOString()
      };
      setDiscounts([...discounts, newDiscount]);
    }
    
    resetDiscountForm();
  };

  const resetDiscountForm = () => {
    setCurrentDiscount({
      id: null,
      title: '',
      description: '',
      coordinates: '',
      category: 'food',
      discountValue: '',
      isActive: true
    });
    setIsEditing(false);
  };

  const editDiscount = (discount) => {
    setCurrentDiscount(discount);
    setIsEditing(true);
  };

  const deleteDiscount = (id) => {
    if (window.confirm(' ,     ?')) {
      setDiscounts(discounts.filter(d => d.id !== id));
    }
  };

  const toggleDiscountActive = (id) => {
    setDiscounts(discounts.map(d => 
      d.id === id ? { ...d, isActive: !d.isActive } : d
    ));
  };

  //    
  const handleProfileSubmit = (e) => {
    e.preventDefault();
    
    if (isEditingProfile) {
      setProfiles(profiles.map(p => 
        p.id === currentProfile.id ? currentProfile : p
      ));
    } else {
      const newProfile = {
        ...currentProfile,
        id: Date.now().toString(),
        createdAt: new Date().toISOString(),
        position: '  ' //  
      };
      setProfiles([...profiles, newProfile]);
    }
    
    resetProfileForm();
  };

  const resetProfileForm = () => {
    setCurrentProfile({
      id: null,
      name: '',
      telegram: '',
      photo: null,
      photoPreview: null,
      isActive: true
    });
    setIsEditingProfile(false);
  };

  const editProfile = (profile) => {
    setCurrentProfile({
      ...profile,
      photoPreview: profile.photo
    });
    setIsEditingProfile(true);
  };

  const deleteProfile = (id) => {
    if (window.confirm(' ,     ?')) {
      setProfiles(profiles.filter(p => p.id !== id));
    }
  };

  const toggleProfileActive = (id) => {
    setProfiles(profiles.map(p => 
      p.id === id ? { ...p, isActive: !p.isActive } : p
    ));
  };

  const handlePhotoUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        setCurrentProfile({
          ...currentProfile,
          photo: e.target.result,
          photoPreview: e.target.result
        });
      };
      reader.readAsDataURL(file);
    }
  };

  const removePhoto = () => {
    setCurrentProfile({
      ...currentProfile,
      photo: null,
      photoPreview: null
    });
  };

  const categories = [
    { value: 'food', label: '' },
    { value: 'entertainment', label: '' },
    { value: 'education', label: '' },
    { value: 'sports', label: '' },
    { value: 'shopping', label: '' },
    { value: 'other', label: '' }
  ];

  //   ,   
  if (!isAuthenticated) {
    return (
      <div className="admin-login">
        <div className="login-container">
          <div className="login-header">
            <h1> </h1>
            <p>  -  </p>
          </div>
          
          <form onSubmit={handleLogin} className="login-form">
            <div className="form-group">
              <label></label>
              <input
                type="text"
                value={loginData.username}
                onChange={(e) => setLoginData({ ...loginData, username: e.target.value })}
                placeholder=" "
                required
              />
            </div>
            
            <div className="form-group">
              <label></label>
              <input
                type="password"
                value={loginData.password}
                onChange={(e) => setLoginData({ ...loginData, password: e.target.value })}
                placeholder=" "
                required
              />
            </div>
            
            <button type="submit" className="btn-primary">
              
            </button>
            
            <div className="login-hint">
              <strong> :</strong><br />
              : admin<br />
              : admin123
            </div>
          </form>
        </div>
      </div>
    );
  }

  //   
  return (
    <div className="admin">
      <header className="admin-header">
        <div className="admin-header-content">
          <div className="admin-title">
            <h1> </h1>
            <p>  -  </p>
          </div>
          
          <div className="admin-controls">
            <div className="admin-url-info">
              <strong> :</strong> {currentSection === 'discounts' ? '' : ''}
            </div>
            <button className="logout-btn" onClick={handleLogout}>
              <LogOut size={16} />
              
            </button>
          </div>
        </div>

        <nav className="admin-nav">
          <button 
            className={`nav-btn ${currentSection === 'discounts' ? 'active' : ''}`}
            onClick={() => setCurrentSection('discounts')}
          >
            <Gift size={18} />
             
          </button>
          <button 
            className={`nav-btn ${currentSection === 'profiles' ? 'active' : ''}`}
            onClick={() => setCurrentSection('profiles')}
          >
            <Users size={18} />
             
          </button>
        </nav>
      </header>

      <div className="admin-container">
        {/*    */}
        {currentSection === 'discounts' && (
          <>
            <div className="form-section">
              <h2>{isEditing ? ' ' : '  '}</h2>
              <form onSubmit={handleDiscountSubmit} className="discount-form">
                <div className="form-row">
                  <div className="form-group">
                    <label>  *</label>
                    <input
                      type="text"
                      value={currentDiscount.title}
                      onChange={(e) => setCurrentDiscount({ ...currentDiscount, title: e.target.value })}
                      placeholder=":    '  '"
                      required
                    />
                  </div>

                  <div className="form-group">
                    <label> *</label>
                    <select
                      value={currentDiscount.category}
                      onChange={(e) => setCurrentDiscount({ ...currentDiscount, category: e.target.value })}
                      required
                    >
                      {categories.map(cat => (
                        <option key={cat.value} value={cat.value}>{cat.label}</option>
                      ))}
                    </select>
                  </div>
                </div>

                <div className="form-group">
                  <label>  *</label>
                  <textarea
                    value={currentDiscount.description}
                    onChange={(e) => setCurrentDiscount({ ...currentDiscount, description: e.target.value })}
                    placeholder="  ,    ..."
                    rows="3"
                    required
                  />
                </div>

                <div className="form-group">
                  <label>  *</label>
                  <input
                    type="text"
                    value={currentDiscount.discountValue}
                    onChange={(e) => setCurrentDiscount({ ...currentDiscount, discountValue: e.target.value })}
                    placeholder="10%, 15%   "
                    required
                  />
                </div>

                <div className="form-group">
                  <label>   (, )</label>
                  <input
                    type="text"
                    value={currentDiscount.coordinates}
                    onChange={(e) => setCurrentDiscount({ ...currentDiscount, coordinates: e.target.value })}
                    placeholder="60.711503, 28.748026"
                  />
                  <small> . : ,   </small>
                </div>

                <div className="form-actions">
                  <button type="submit" className="btn-primary">
                    <Save size={16} />
                    {isEditing ? ' ' : ' '}
                  </button>
                  {isEditing && (
                    <button type="button" onClick={resetDiscountForm} className="btn-secondary">
                      
                    </button>
                  )}
                </div>
              </form>
            </div>

            <div className="discounts-section">
              <div className="section-header">
                <h2>  ({discounts.filter(d => d.isActive).length})</h2>
                <div className="stats">
                  : {discounts.length} | : {discounts.filter(d => d.isActive).length}
                </div>
              </div>

              {discounts.length === 0 ? (
                <div className="empty-state">
                  <div className="empty-icon">
                    <Plus size={48} />
                  </div>
                  <h3>  </h3>
                  <p>  ,   </p>
                </div>
              ) : (
                <div className="discounts-grid">
                  {discounts.map(discount => (
                    <div key={discount.id} className={`discount-card ${!discount.isActive ? 'inactive' : ''}`}>
                      <div className="discount-header">
                        <div className="discount-title">
                          <h3>{discount.title}</h3>
                          <span className={`category-badge ${discount.category}`}>
                            {categories.find(c => c.value === discount.category)?.label}
                          </span>
                        </div>
                        <div className="discount-actions">
                          <button 
                            className="icon-btn edit-btn"
                            onClick={() => editDiscount(discount)}
                            title=""
                          >
                            <Save size={16} />
                          </button>
                          <button 
                            className="icon-btn delete-btn"
                            onClick={() => deleteDiscount(discount.id)}
                            title=""
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </div>

                      <div className="discount-body">
                        <p className="discount-description">{discount.description}</p>
                        
                        <div className="discount-details">
                          <div className="detail-item">
                            <strong>:</strong>
                            <span className="discount-value">{discount.discountValue}</span>
                          </div>
                          
                          {discount.coordinates && (
                            <div className="detail-item">
                              <strong>:</strong>
                              <span className="coordinates">
                                <MapPin size={12} />
                                {discount.coordinates}
                              </span>
                            </div>
                          )}
                        </div>
                      </div>

                      <div className="discount-footer">
                        <div className="status-toggle">
                          <label className="toggle-label">
                            <input
                              type="checkbox"
                              checked={discount.isActive}
                              onChange={() => toggleDiscountActive(discount.id)}
                            />
                            <span className="toggle-slider"></span>
                            <span className="toggle-text">
                              {discount.isActive ? '' : ''}
                            </span>
                          </label>
                        </div>
                        <div className="discount-date">
                          : {new Date(discount.createdAt).toLocaleDateString('ru-RU')}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </>
        )}

        {/*    */}
        {currentSection === 'profiles' && (
          <>
            <div className="form-section">
              <h2>{isEditingProfile ? ' ' : '  '}</h2>
              <form onSubmit={handleProfileSubmit} className="profile-form">
                <div className="form-group">
                  <label>  *</label>
                  <input
                    type="text"
                    value={currentProfile.name}
                    onChange={(e) => setCurrentProfile({ ...currentProfile, name: e.target.value })}
                    placeholder="  "
                    required
                  />
                </div>

                <div className="form-group">
                  <label>Telegram username *</label>
                  <input
                    type="text"
                    value={currentProfile.telegram}
                    onChange={(e) => setCurrentProfile({ ...currentProfile, telegram: e.target.value })}
                    placeholder="@username"
                    required
                  />
                  <small>  @username</small>
                </div>

                <div className="form-group">
                  <label> </label>
                  <div className="photo-upload-section">
                    {currentProfile.photoPreview ? (
                      <div className="photo-preview">
                        <img src={currentProfile.photoPreview} alt="Preview" />
                        <button type="button" onClick={removePhoto} className="remove-photo-btn">
                           
                        </button>
                      </div>
                    ) : (
                      <div className="photo-upload-placeholder">
                        <label htmlFor="photo-upload" className="photo-upload-label">
                          <Camera size={32} />
                          <span> </span>
                          <input
                            id="photo-upload"
                            type="file"
                            accept="image/*"
                            onChange={handlePhotoUpload}
                            className="photo-input"
                          />
                        </label>
                      </div>
                    )}
                  </div>
                  <small> : 400x400px</small>
                </div>

                <div className="form-actions">
                  <button type="submit" className="btn-primary">
                    <Save size={16} />
                    {isEditingProfile ? ' ' : ' '}
                  </button>
                  {isEditingProfile && (
                    <button type="button" onClick={resetProfileForm} className="btn-secondary">
                      
                    </button>
                  )}
                </div>
              </form>
            </div>

            <div className="profiles-section">
              <div className="section-header">
                <h2>  ({profiles.filter(p => p.isActive).length})</h2>
                <div className="stats">
                  : {profiles.length} | : {profiles.filter(p => p.isActive).length}
                </div>
              </div>

              {profiles.length === 0 ? (
                <div className="empty-state">
                  <div className="empty-icon">
                    <Users size={48} />
                  </div>
                  <h3>  </h3>
                  <p>  ,   </p>
                </div>
              ) : (
                <div className="profiles-grid">
                  {profiles.map(profile => (
                    <div key={profile.id} className={`profile-card ${!profile.isActive ? 'inactive' : ''}`}>
                      <div className="profile-header">
                        <div className="profile-info">
                          <div className="profile-avatar">
                            {profile.photo ? (
                              <img src={profile.photo} alt={profile.name} />
                            ) : (
                              <div className="avatar-placeholder">
                                {profile.name.split(' ').map(n => n[0]).join('').toUpperCase()}
                              </div>
                            )}
                          </div>
                          <div className="profile-title">
                            <h3>{profile.name}</h3>
                            <span className="position-badge">
                                
                            </span>
                            {profile.telegram && (
                              <div className="telegram-link">
                                @{profile.telegram.replace('@', '')}
                              </div>
                            )}
                          </div>
                        </div>
                        <div className="profile-actions">
                          <button 
                            className="icon-btn edit-btn"
                            onClick={() => editProfile(profile)}
                            title=""
                          >
                            <Save size={16} />
                          </button>
                          <button 
                            className="icon-btn delete-btn"
                            onClick={() => deleteProfile(profile.id)}
                            title=""
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </div>

                      <div className="profile-footer">
                        <div className="status-toggle">
                          <label className="toggle-label">
                            <input
                              type="checkbox"
                              checked={profile.isActive}
                              onChange={() => toggleProfileActive(profile.id)}
                            />
                            <span className="toggle-slider"></span>
                            <span className="toggle-text">
                              {profile.isActive ? '' : ''}
                            </span>
                          </label>
                        </div>
                        <div className="profile-date">
                          : {new Date(profile.createdAt).toLocaleDateString('ru-RU')}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Admin;